import ProjectCard from '../../components/ProjectCard'

const allProjects = [
  {
    id: '1',
    title: 'Bursary Management System',
    description: 'A comprehensive PHP-based bursary management system for student applications',
    technologies: ['PHP', 'MySQL', 'JavaScript', 'Bootstrap'],
    githubUrl: 'https://github.com/ogbex101/bursary-system',
    liveUrl: null,
    category: 'PHP',
    featured: true
  }
  // Add more projects here as you upload them
]

export default function ProjectsPage() {
  return (
    <div className="max-w-6xl mx-auto px-4 py-8">
      <h1 className="text-4xl font-bold mb-8">All Projects</h1>
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {allProjects.map((project) => (
          <ProjectCard key={project.id} project={project} />
        ))}
      </div>
      {allProjects.length === 0 && (
        <div className="text-center py-16">
          <p className="text-gray-500 text-lg">No projects yet. Check back soon!</p>
        </div>
      )}
    </div>
  )
}